.search-bar-container{max-width:400px;display:block;margin:100px}.player-search-input{height:30px;width:300px;border-radius:3px}#search-bar{box-sizing:border-box;height:50px;width:300px;padding:15px 15px;border-radius:5px}.search-bar-results{padding:10px 5px;padding:15px 15px;max-height:150px;overflow-y:auto;border-left:1px solid;border-bottom:1px solid;border-right:1px solid;background:white;box-sizing:border-box}.search-bar-results li{list-style:none;padding:10px 25px;width:100px;cursor:default;border-radius:2px;z-index:3}
